    /* Agrega este código debajo de las reglas CSS existentes */
    .abajo {
        display: flex;
        justify-content: center; /* Centrar horizontalmente */
        align-items: center; /* Centrar verticalmente */
        background-color: #000000; /* Color de fondo del footer */
        color: #fff; /* Color del texto en el footer */
        padding: 20px; /* Espaciado interno */
    }

    .redes-sociales {
        display: flex;
        gap: 20px; /* Espaciado entre los iconos de redes sociales */
    }

    .social-icon {
        font-size: 24px; /* Tamaño de fuente de los iconos */
        color: #fff; /* Color de los iconos */
        text-decoration: none; /* Eliminar subrayado enlaces */
        transition: color 0.3s; /* Transición de color al pasar el mouse */
    }

    .social-icon:hover {
        color: #007bff; /* Cambiar el color al pasar el mouse */
    }


